import ApplicationBabelHTML5Base from "jx/projecttypes/ApplicationBabelHTML5Base";
import Importer from "jx/projecttypes/editorformattype1/Importer";
import Exporter from "jx/projecttypes/editorformattype1/Exporter";
export default class ProjectType1 extends ApplicationBabelHTML5Base {
    constructor(dataObject) {
        super(dataObject);
    }
    _create() {
        super._create();
        this.importer = new Importer(this.jx, this.info);
        this.exporter = new Exporter(this.jx, { projectInfo: this.info });
    }
    /** Show preview of project */
    preview(rid = null) {
        this.jx.tools.openURL(this.info.url + "/public" + (rid ? "/?rid=" + rid : ""), rid || this.info.id);
    }
}
